  color: rgba(0, 0, 0, 0.4);
  margin-top: 5px;
  text-align: right;
}

.self-bubble .message-time {
  /* color: rgba(255, 255, 255, 0.7); */
}

.chat-input {
  padding: 15px;
  border-top: 1px solid var(--border-color);
  display: flex;
  gap: 10px;
}

.chat-input .el-textarea {
  flex: 1;
}

.no-conversation, .empty-chat {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
