  text-align: center;
}

.no-conversation i, .empty-chat i {
  font-size: 60px;
  margin-bottom: 20px;
  color: #ccc;
}

/*  */
@media (max-width: 992px) {
  .messages-container {
    grid-template-columns: 300px 1fr;
  }
}

@media (max-width: 768px) {
  .messages-container {
    grid-template-columns: 1fr;
  }
  
  .message-list-container {
    display: block;
  }
  
  .hidden-mobile {
    display: none;
  }
  
  .chat-container {
    display: none;
  }
  
  .active-mobile {
    display: flex;
  }
  
  .back-to-list {
    display: block;
  }
}

@media (max-width: 576px) {
  .header {
    padding: 15px;
  }
  
  /* .back-button {
    padding: 8px 16px;
    font-size: 14px;
  }
   */
